.page-title {
	border-bottom: 1px solid rgba(#000, .06);
	margin: 0;
	padding: 10px 60px;
	position: relative;

  	@media (max-width: $media-md) {
		padding-left: 30px;
	  	padding-right: 30px;
	}

	.has-page-header & {
		border-bottom-color: rgba(#fff, .2);
	}

	h1 {
		font-size: 20px;
		font-weight: 400;
		margin: 0 0 2px 0;
		padding: 0;

		.has-page-header & {
			color: #fff;
			text-shadow: 1px 1px rgba(#000, .3);
		}
	}

	ul {
		@include susy-clearfix();

		font-size: 13px;
		list-style: none;
		margin: 0 0 6px 0;
		padding: 0;
	}

	li {
		opacity: .6;
		float: left;

		&:after {
			content: '/';
			display: inline-block;
			margin: 0 10px;
			opacity: .5;
		}

		&:last-child {
			&:after {
				display: none;
			}
		}

		a {
			.has-page-header & {
				color: #fff;
				text-shadow: 1px 1px rgba(#000, .3);
			}
		}
	}

	.shortcuts {
		@media (max-width: $media-md) {
			display: none;
		}
	}

}



