html {
	height: 100%;
}

body {
	-webkit-font-smoothing: antialiased;
	color: $color-font;
	font-family: BlinkMacSystemFont, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
	font-size: 13px;
	height: 100%;

    &.panel-open {
      // disable scrolling on the body when panels are open
      overflow:hidden;
    }
}

a {
	color: $color-font;
	text-decoration: none;
}

.md-icon {
  -webkit-font-smoothing: antialiased;
  font-family: 'Material Icons';
  text-rendering: optimizeLegibility;
  font-feature-settings: "liga" 1;
  font-style: normal;
  text-transform: none;
  line-height: 1;
  font-size: 20px;
  width: 20px;
  height: 20px;
  display: inline-block;
  overflow: hidden;
}

.icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 -10px;
}

.icon {
  border: 1px solid rgba($color-black, .06);
  margin: 10px;
  padding: 16px;
}

.row {
  display: flex;
  flex-direction: row;
  margin: 0 -15px;

  > div {
    flex-grow: 1;
    margin: 0 15px;
    width: 50%;
  }
}

.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}

label {
  cursor: pointer;
  display: block;
  margin: 0;
}


